.rpv-scroll-mode-vertical {
  display: flex;
  flex-direction: column;
}
.rpv-scroll-mode-horizontal {
  display: flex;
  flex-direction: row;
}
.rpv-scroll-mode-wrapped {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
