.rpv-drop-area {
  align-items: center;
  background-color: #ffffff;
  border: 2px dashed rgba(0, 0, 0, 0.3);
  bottom: 0;
  display: flex;
  font-size: 24px;
  justify-content: center;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 9999;
}
